*{font-family:'Open Sans', sans-serif}body{margin:0;padding:0}.container-movie{all:unset;cursor:pointer;height:290px;width:198px;display:flex;flex-direction:column;justify-content:flex-end;color:#ecf0f1;margin:10px;background-position:center;background-size:cover}.container-movie .info{display:flex;flex-direction:column;justify-content:flex-end;height:290px;background:linear-gradient(0deg, #000 0%, rgba(0,0,0,0) 45%);padding:0 16px 16px 16px}.container-movie .info .box{transform:scale(0.9);opacity:0}.container-movie .info .mainInfo .title{font-size:12px;text-transform:uppercase;font-weight:600}.container-movie .info .mainInfo .genres{display:flex;flex-wrap:wrap}.container-movie .info .mainInfo .genres .genre{font-size:10px;margin:4px 8px 0 0}.container-movie .info .infoHoverWrapper{display:flex;justify-content:space-between;transform:scale(0.9);opacity:0}.container-movie .info .infoHoverWrapper .infoHover{font-size:12px;font-weight:600}@media screen and (max-width: 770px){.container-movie{height:242px;width:164px;margin:5px}}.container-movie:hover .info{height:290px;background:linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.5) 43.25%, #000)}.container-movie:hover .info .box{height:100%;display:flex;justify-content:center;align-items:center;transition:500ms linear;transform:scale(1);opacity:1}.container-movie:hover .info .box .progress{color:rgba(216,216,216,0.15);min-height:80px;min-width:80px}.container-movie:hover .info .box .progressBg{position:absolute;color:#FFE000;min-height:80px;min-width:80px}@media screen and (max-width: 770px){.container-movie:hover .info .box .progress{min-height:60px;min-width:60px}}@media screen and (max-width: 770px){.container-movie:hover .info .box .progressBg{min-height:60px;min-width:60px}}.container-movie:hover .info .box .boxWrapper{position:absolute;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center}.container-movie:hover .info .box .boxWrapper span{font-size:20px;font-weight:600}@media screen and (max-width: 770px){.container-movie:hover .info .box{margin-bottom:40px}}.container-movie:hover .info .mainInfo{transform:translateY(-20px);transition:transform 500ms}.container-movie:hover .info .infoHoverWrapper{transition:500ms linear;transform:scale(1);opacity:1}

